@charset "UTF-8";
/* 리셋 CSS - reset.css */
html, body, ul, ol, li, div, span, article, aside, section, h1, h2, h3, h4, h5, input, audio, button, p, dl, dt, dd,
figure, figcaption {
  margin: 0;
  padding: 0;
}

body {
  overflow-x: hidden;
}

a {
  color: #000;
  text-decoration: none;
}

li {
  list-style: none;
}

img {
  border: none;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  display: block;
}

* {
  font-family: "Noto Sans KR";
  font-style: normal;
  box-sizing: border-box;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}

input, button {
  border: none;
}

select {
  outline: none;
}/*# sourceMappingURL=index.css.map */